import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import Dropdown from 'react-dropdown';
import {Box, TextField} from "@material-ui/core";
import { PREDEFINED_LOGOS } from "./konfiguracja/reducers";
import KopiujDoURL from "./KopiujDoURL";
import { ReactInstance } from "react";
import {changeAutor, changeCytat, changeLogo, changeTytul} from "./konfiguracja/actions";

interface UstawieniaProps extends PropsFromRedux {
    componentRef: React.RefObject<ReactInstance>
}

class UstawieniaInternal extends React.Component<UstawieniaProps> {
    render() {
        const {autor, componentRef, cytat, tytul, changeAutor, changeCytat, changeLogo, changeTytul} = this.props;
        return <Box display="flex" flexDirection="column" className="accordionContent">
                    <span className="hint">Logo w nagwku:</span>
                    <Dropdown
                        options={PREDEFINED_LOGOS}
                        value={PREDEFINED_LOGOS[2]}
                        onChange={(input)=>changeLogo(input.value)}
                        placeholder='Wybierz logo...'>
                    </Dropdown>
                    {/*<input type="text" name="logo_url" onChange={ev=>changeLogoUrl(ev.target.value)} value={logo_url}/>*/}
                    <Box display="flex" flexDirection="column" className="accordionContent" >
                      <TextField label="Tytu infografiki" type="text" name="tytul" onChange={ev=>changeTytul(ev.target.value)} value={tytul}/>
                      <TextField label="Tekst" type="text" name="cytat" onChange={ev=>changeCytat(ev.target.value)} value={cytat} multiline/>
                      <TextField label="Autor" type="text" name="autor" onChange={ev=>changeAutor(ev.target.value)} value={autor}/>
                    </Box>
                    <KopiujDoURL componentRef={componentRef}/>
                </Box>
    }
}

const mapStateToProps = (state: RootState) => {
  return {
    gmina: state.dane_gminy.gmina,
    kod_gminy: state.kod_gminy,
    autor: state.autor,
    cytat: state.cytat,
    tytul: state.tytul,
    logo: state.logo,
  };
};

const mapDispatchToProps = {
  changeLogo,
  changeAutor,
  changeCytat,
  changeTytul,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(UstawieniaInternal);