import React from "react";
import {Box} from "@material-ui/core";
import {
  Bar,
  BarChart,
  Cell, LabelList,
  XAxis,
  YAxis
} from "recharts";

import {Wykres} from "./infografika/Wykres";
import {autoJednostka, COLOR_MAP, getDivider} from "./wykresyUtil";

export class WykresWojewodztw extends React.Component<Wykres> {
  render() {
    const {dane_kolowy = [], opis = "Wydatki w porwnaniu do innych jednostek", jednostka = "auto", per_capita = true, ludnosc} = this.props;
    if (opis === '') {
      return null;
    }
    const pc_divider = per_capita?ludnosc || 1:1;
    let daneFinal: object[] = [];
    const max = dane_kolowy.map(value => value.value).reduce((preVal, curVal) => Math.max(preVal, curVal), 0);
    dane_kolowy.forEach((entry, index) => {
      daneFinal.push({
        labelka: entry.name,
        gmina1: entry.value / pc_divider,
        // Using space as a key prevents from it showing up in the legend
        max: max,
      })
    });
    const commonChartProps = {
      syncId: opis,
      width: 1200,
      height: 520,
      data: daneFinal,
      margin: {top: 10, right: 30, left: 0, bottom: 10},
    }
    const j = autoJednostka(jednostka, max);
    const numberDivider = (value: number) => {
      return Math.floor(value / (getDivider(j) * pc_divider));
    }
    const x = () => { return <XAxis type="number" orientation="bottom" xAxisId="bottom" tickSize={12} padding={{ left: 0, right: 50 }} interval="preserveEnd" label={{value: j, position: "insideTopRight", offset: -25}} tickFormatter={numberDivider}/>}
    const y = () => { return <YAxis hide={true} type="category" tick={false} /> }
    return <>
      <Box textAlign="center"><b>{opis}</b></Box>
      <BarChart {...commonChartProps} layout='vertical' barCategoryGap={0} barGap={0}>
        {x()} {y()}
        <Bar key={"porownawcze_dane-0"}
             stackId={0}
             layout="horizontal"
             name={"gmina1"}
             dataKey={"gmina1"}
             xAxisId="bottom"
             barSize={25}
             style={{zIndex: -1}}>
          {daneFinal.map((entry, index) => {
            return <Cell height={25} style={{zIndex: -1}} key={index} fill={COLOR_MAP.odcienie[0][Math.floor(index * (COLOR_MAP.odcienie[0].length) / daneFinal.length)]}/>
          })}
        </Bar>
        <Bar key={"porownawcze_dane-1"}
             stackId={1}
             dataKey="max" fill="#fefefe01" xAxisId="bottom" barSize={1}>
          <LabelList dataKey="labelka" position="insideBottomLeft" className="labelka"/>
        </Bar>
      </BarChart>
    </>;
  }
}