import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import {Box, Button, Container} from "@material-ui/core";

class DodatkoweInternal extends React.Component<PropsFromRedux> {
    render() {
        const {kod_gminy, gmina} = this.props;
        return <Box display="flex" flexDirection="row" className="accordionContent">
                  <Container>
                      <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_ue#`} variant="outlined" color="primary">
                          Projekty UE
                      </Button>
                  </Container>
                  <Container>
                      <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_sunburst#`} variant="outlined" color="primary">
                          Wydatki
                      </Button>
                  </Container>
                  <Container>
                      <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_sunburst_dochody#`} variant="outlined" color="primary">
                          Dochody
                      </Button>
                  </Container>
              </Box>
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        gmina: state.dane_gminy.gmina,
        kod_gminy: state.kod_gminy,
    };
};

const connector = connect(
    mapStateToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(DodatkoweInternal);