import { Button } from "@material-ui/core";
import React from "react";
import {ReactReduxContext} from "react-redux";
import {JSONCrush} from "jsoncrush"
import { ReactInstance } from "react";


interface KopiujDoURLProps {
    componentRef: React.RefObject<ReactInstance>;
}

class KopiujDoURLInternal extends React.Component<KopiujDoURLProps> {
    render() {
        return <ReactReduxContext.Consumer>
            {({store}) => {
                return <React.Fragment>
                    <Button variant="contained" color="secondary" onClick={() => {
                        const {dane_gminy, lista_powiatow, dane_podregionu, porownawcze_dane_gminy, ...state} = store.getState();
                        const str = window.location.href.split('?')[0] + '?s=' + JSONCrush(JSON.stringify(state));
                        navigator.clipboard.writeText(str).then(function () {
                              console.log("Copied to clipboard");
                          }, function () {
                              console.log("Copy to clipboard failed!!");
                          }
                        );
                    }}>
                        Skopiuj roboczy link do schowka
                    </Button>
                </React.Fragment>
            }}
        </ReactReduxContext.Consumer>;
    };
}

export default KopiujDoURLInternal;