import {connect, ConnectedProps} from 'react-redux'
import React, {FormEvent} from 'react';
import './App.css';

import Box from "@material-ui/core/Box";
import Panel from "./Panel";
import WyborModulow from "./WyborModulow";
import {SelektorGrupy} from "./SelektorGrupy";
import {
    changeDzial,
    pobierzListePowiatow
} from "./konfiguracja/actions";
import {RootState} from "./reducers";
import {
    changeGrupaDzial,
    changeGrupaRozdzial,
} from "./infografika/actions";
import Wykresy from "./Wykresy";
import {Accordion, AccordionSummary, Button, Modal} from "@material-ui/core";
import {ExpandMore} from "@material-ui/icons";
import {Tour} from "./Tour";
import 'react-dropdown/style.css';
import { createStyles, Theme, withStyles } from "@material-ui/core/styles";
import Ustawienia from './Ustawienia';
import SelektorGminy from "./SelektorGminy";
import {Predefiniowane} from "./Predefiniowane";
import Udostepnij from "./Udostepnij";
import Dodatkowe from "./Dodatkowe";

const styles = (theme: Theme) => createStyles({
    root: {
      backgroundColor: "red"
    }
  });

interface AppState {
    pomocOtwarta: boolean;
    tour: boolean;
    componentRef : React.RefObject<any>;
    accExpanded: boolean[];
    accExpandedTmp: boolean[];
}

class App extends React.Component<PropsFromRedux, AppState> {
    classes : any;

    constructor(props: PropsFromRedux) {
        super(props);
        this.state = {
            pomocOtwarta: false,
            tour: false,
            componentRef: React.createRef(),
            accExpanded: [true, false, false, false, false],
            accExpandedTmp: [true, false, false, false, false],
        }
        this.handleSubmit = this.handleSubmit.bind(this);

    }

    handleSubmit(event: FormEvent) {
        event.preventDefault();
    }

    isShown(name: string) {
        return (this.props.moduly.indexOf(name) > -1)
    }

    renderPomocModal() {
        const zamknijPomoc = () => {
            this.setState( {...this.state, pomocOtwarta: false, accExpanded: this.state.accExpandedTmp});
        }

        return <Modal open={this.state.pomocOtwarta} onClose={zamknijPomoc}>
            <div className="modalStyle paper">
                <div>Problem z generowaniem infografiki?<br/><br/>
                    Napisz do nas: <a href="mailto:pomoc@infografika.edu.pl">pomoc@infografika.edu.pl</a><br/><br/></div>
                <div className="zamknijPomoc"><button onClick={(ev) => zamknijPomoc()}>Zamknij</button></div>
            </div>
        </Modal>

    }

    render() {
        const otworzPomoc = () => {
            this.setState({...this.state, pomocOtwarta: true})
        }

        const flip = (num: number) => {
          let accTmp = [
            ...this.state.accExpanded.slice(0, num),
            !this.state.accExpanded[num],
            ...this.state.accExpanded.slice(num + 1, this.state.accExpanded.length)];
          this.setState({...this.state, accExpanded: accTmp});
        }

        const {dzial} = this.props;
        const {changeDzial} = this.props;
        return (
            <Box display="flex" flexDirection="row">
                <Panel ref={this.state.componentRef} />
                <form onSubmit={this.handleSubmit} className="formularzContainer">
    <Box display="flex" flexDirection="column" className="formularz">
        <div id="warning" />
        {this.renderPomocModal()}
        <Tour enabled={this.state.tour} finishedCallback={() => this.setState({...this.state, accExpanded: this.state.accExpandedTmp, tour: false})}/>
        <Box display="flex" flexDirection="row">
            <Predefiniowane />
            <Button id="start-tour" variant="contained" color="secondary" onClick={() => {this.setState({...this.state, tour: true, accExpandedTmp: this.state.accExpanded, accExpanded: [true, true, true, true, true]})}}>przewodnik</Button>&nbsp;&nbsp;&nbsp;
            <Button variant="outlined" color="primary" target="_blank" rel="noreferrer" href="https://docs.google.com/document/d/1HPV_g1jvNsFqBr4GZ54CXvM86XDD6QxwUPMKOCvcB38/edit#">Baza Wiedzy</Button>&nbsp;&nbsp;&nbsp;
            <Button variant="outlined" color="primary" onClick={() => {otworzPomoc();}}>kontakt</Button>
        </Box>
        <Accordion expanded={this.state.accExpanded[0]} className="acc-udostepnij">
            <AccordionSummary onClick={() => flip(0)} className="accordion" expandIcon={<ExpandMore />}>Udostpnij na FB/IG</AccordionSummary>
            <Udostepnij componentRef={this.state.componentRef} />
        </Accordion>
        <Accordion expanded={this.state.accExpanded[1]} className="acc-ustawienia">
            <AccordionSummary onClick={() => flip(1)}  className="accordion" expandIcon={<ExpandMore />}>Ustawienia Infografiki</AccordionSummary>
                <Ustawienia componentRef={this.state.componentRef} />
        </Accordion>
        <Accordion expanded={this.state.accExpanded[2]} className="acc-budzet">
            <AccordionSummary onClick={() => flip(2)}  className="accordion" expandIcon={<ExpandMore />}>Moduy Budetowe</AccordionSummary>
            <Box display="flex" flexDirection="column" className="accordionContent" >
                <span className="hint">Wybr moduw do wywietlenia:</span>
                <WyborModulow />
                {this.isShown('dzialy-porownawczy')?<>
                        <span className="hint">Dane Porwnawcze dla:</span>
                        <SelektorGminy porownawcze={true}/></>:null}
                {(this.isShown('rozdzialy') || this.isShown('rozdzialy-per-capita'))?<React.Fragment>
                        <strong>Number dziau do szczegowego wywietlenia:</strong>
                        <input type="text" name="dzial" value={dzial}
                               onChange={ev=>changeDzial(ev.target.value)}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
                {this.isShown('grupy-dzialy')?<React.Fragment>
                        <strong>Podzia wydatkw z grupy na dziay:</strong>
                        <SelektorGrupy grupaChangeFn={this.props.changeGrupaDzial}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
                {this.isShown('grupy-rozdzialy')?<React.Fragment>
                        <strong>Podzia wydatkw z grupy na rozdziay:</strong>
                        <SelektorGrupy grupaChangeFn={this.props.changeGrupaRozdzial}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
            </Box>
        </Accordion>
        <Accordion expanded={this.state.accExpanded[3]} className="acc-wykresy">
            <AccordionSummary onClick={() => flip(3)}  className="accordion" expandIcon={<ExpandMore />}>Wykresy</AccordionSummary>
            <Box display="flex" flexDirection="column" className="accordionContent wykresy" >
                <Box><strong><a target="_blank" rel="noreferrer" href="https://bdl.stat.gov.pl/BDL/metadane/cechy/2398#">Dane dostpne z GUS</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="https://docs.google.com/spreadsheets/d/1Xwikh1KvRrN_PW5LPobB8wnQey3LtzXK-3yyuYSFPg0/edit#gid=428530918">Arkusz ze wszystkimi zmiennymi GUS</a></strong></Box>
                <span className="hint" style={{color: "red"}}>(Puste wykresy GUS to zwykle pobieranie danych na zlym poziomie - np. dane s na poziomie powiatu a damy dla gminy)</span>
                <Wykresy/>
            </Box>
        </Accordion>
        <Accordion expanded={this.state.accExpanded[4]} className="acc-dodatkowe">
            <AccordionSummary onClick={() => flip(4)}  className="accordion" expandIcon={<ExpandMore />}>Dodatkowe informacje o gminie/powiecie</AccordionSummary>
            <Dodatkowe/>
        </Accordion>
    </Box>
                </form>
            </Box>
        );
    }

    componentDidMount() {
        this.props.pobierzListePowiatow(this.props.wojewodztwo, this.props.kod_gminy);
        // setTimeout(() => this.setState({...this.state, tour: true}), 5000);
        this.resize();
        window.addEventListener("resize", this.resize);
    }

    resize() {
        if (window.innerWidth < 800) {
            // @ts-ignore
            document.getElementById("root").className = "super_male_okno";
        } else if (window.innerWidth < 1500) {
            // @ts-ignore
            document.getElementById("root").className = "male_okno";
        } else {
            // @ts-ignore
            document.getElementById("root").className = "";
        }
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        kod_gminy: state.kod_gminy,
        dane_gminy: state.dane_gminy,
        wojewodztwo: state.wojewodztwo,
        lista_powiatow: state.lista_powiatow,
        moduly: state.moduly,
        dzial: state.dzial,
    };
};

const mapDispatchToProps = {
    changeDzial,
    changeGrupaDzial,
    changeGrupaRozdzial,
    pobierzListePowiatow,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(withStyles(styles, { withTheme: true})(App));