import React from "react";
import ReactJoyride from "react-joyride";

interface Props {
    enabled: boolean;
    finishedCallback: any;
}

export class Tour extends React.Component<Props> {
    static STEPS = [
        {
            target: '#main_section',
            content: 'Generator pozwala Ci stworzy grafik do publikacji w mediach spoecznociowych z danymi z dowolnej gminy.',
            disableBeacon: true,
        },
        {
            target: '.predefiniowane',
            content: 'Moesz skorzysta z ju zdefiniowanych infografik. Wybierz temat, ktry Ci interesuje.',
        },
        {
            target: '.acc-udostepnij',
            content: 'Wybierz swoj gmin, powiat lub cae wojewdztwo i wygeneruj infografik z wybranego szablonu. Plikiem moesz podzieli si np. na Facebooku lub Instagramie. Wygenerujemy te krtki link pod ktrym inni bd mogli j znale.',
        },
        {
            target: '.acc-ustawienia',
            content: 'Tutaj moesz zmieni to i logo, wyedytowa tekst i podpisa Twoj unikaln infografik. Jeli z kim jeszcze nad ni pracujesz to moesz tutaj te skopiowa link do wersji roboczej i wysa j mailem lub komunikatorem.',
        },
        {
            target: '.acc-komentarz',
            content: 'W tym miejscu dodasz komentarz (Twj, eksperta lub polityka) do infografiki.',
        },
        {
            target: '.acc-budzet',
            content: 'Tutaj wczysz lub wyczysz pokazywanie moduw dotyczcych budetu danej gminy oraz ustawisz ich opcje.',
        },
        {
            target: '.acc-wykresy',
            content: 'A tutaj moesz doda lub usun wykres. Pobierze on automatycznie dane z GUS, albo atwo moesz wklei wasne.',
        },
        {
            target: '.acc-dodatkowe',
            content: 'Chcesz wiedzie wicej o wybranej jednostce samorzdu? Kliknij tutaj aby otworzy dodatkowe dane w nowej zakadce.',
        },
        {
            target: '#start-tour',
            content: 'Chcesz skorzysta z samouczka jeszcze raz? Kliknij tutaj. Klik, klik.',
        },
    ]

    render () {
        const { enabled, finishedCallback } = this.props;
        // @ts-ignore
        const callback = data => {
            const { action } = data;
            if (action === "reset" || action === "close") {
                finishedCallback();
            }
        };
        return (
            <div className="app">
                <ReactJoyride run={enabled}
                              steps={Tour.STEPS} continuous={true} showSkipButton={true} showProgress={true}
                              callback={callback}
                              styles={{
                                  options: {
                                      primaryColor: '#f9c00c',
                                  },
                                  tooltipContainer: {
                                      textAlign: "justify",
                                  }
                              }}

                              locale={{
                                  next: "Dalej",
                                  last: "Zamknij",
                                  skip: "Pomi",
                                  back: "Wstecz"
                              }}
                />
            </div>)
    }

}