import {
    ADD_WYKRES,
    CHANGE_GRUPA_DZIAL,
    CHANGE_GRUPA_ROZDZIAL, DELETE_WYKRES, WykresAction,
    MOVE_DOWN_WYKRES, MOVE_UP_WYKRES, SET_WYKRES,
    StringAction, usunSerieGUSAction,
} from "../action";
import {SeriaGUS, Wykres, wykresyGUSToIdArray} from "./Wykres";

export const grupa_dzial = (state = 'Wynagrodzenia i skadki', action: StringAction) => {
    switch (action.type) {
        case CHANGE_GRUPA_DZIAL:
            return action.payload;
        default:
            return state;
    }
};

export const grupa_rozdzial = (state = 'Wynagrodzenia i skadki', action: StringAction) => {
    switch (action.type) {
        case CHANGE_GRUPA_ROZDZIAL:
            return action.payload;
        default:
            return state;
    }
};

const initial_wykresy: Wykres[] = [
    {
        gus: {
            "397594": {
                "id": 397594,
            },
            "397595": {
                "id": 397595,
            },
        },
        "typ_wykresu": "GUS: Kolumnowy",
        "opis": "Samorzd wedug pci (w procentach)",
        nazwy_serii: "mczyni,kobiety",
        "gus_lata": 10
    }
  ];

export const wykresy = (state = initial_wykresy, action: WykresAction | usunSerieGUSAction) => {
    console.log(action);
    if (action.type === "USUN_SERIE_GUS") {
        const { index, pozostale_serie_gus } = action.payload;
        if (index === undefined || pozostale_serie_gus === undefined) {
            console.log("Brak indexu lub serii gus do usunicia");
            return state;
        }
        const wszystkieSerie = wykresyGUSToIdArray(state[index]);
        console.log(wszystkieSerie);
        let newGUS: {[k: number]: SeriaGUS} = {};
        console.log(newGUS);
        wszystkieSerie.filter(value => pozostale_serie_gus.indexOf(+value) === -1).forEach((value) => {
            console.log(value);
            // @ts-ignore
            // newGUS[+value] = {...state[index].gus[+value]};
        });
        console.log(newGUS);

        return [
            ...state.slice(0, index),
            {...(state[index]), gus: newGUS},
            ...state.slice(index + 1)
        ];
    }
    if (["ADD_WYKRES", "DELETE_WYKRES", "SET_WYKRES", "MOVE_DOWN_WYKRES", "MOVE_UP_WYKRES", "SET_SERIE_GUS"].indexOf(action.type) === -1) {
        return state;
    }
    const { index, wykres } = action.payload;
    switch (action.type) {
        case ADD_WYKRES:
            if (wykres === undefined) {
                console.log("Brak wartosci dla ADD_WYKRES");
                return state;
            }
            return [...state, wykres];
        case DELETE_WYKRES:
            if (index === undefined) {
                console.log("Brak indexu dla DELETE_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index),
                ...state.slice(index + 1)
            ];
        case SET_WYKRES:
            if (index === undefined || wykres === undefined) {
                console.log("Brak indexu lub gus_input dla SET_WYKRES");
                return state;
            }
            if (wykres.gus !== undefined) {
                // Opisy serii GUS czymy a reszt nadpisujemy
                const gus = {...((state[index]).gus), ...wykres.gus};
                return [
                    ...state.slice(0, index),
                    {...(state[index]), ...wykres, gus: gus},
                    ...state.slice(index + 1)
                ];
            }
            return [
                ...state.slice(0, index),
                {...(state[index]), ...wykres},
                ...state.slice(index + 1)
            ];
        case MOVE_UP_WYKRES:
            if (index === undefined || index < 1) {
                console.log("Brak lub zly indeks dla MOVE_UP_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index - 1),
                ...state.slice(index, index + 1),
                ...state.slice(index - 1, index),
                ...state.slice(index + 1)
            ]
        case MOVE_DOWN_WYKRES:
            if (index === undefined || index > state.length - 2) {
                console.log("Brak lub zly indeks dla MOVE_DOWN_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, index),
                ...state.slice(index + 1, index + 2),
                ...state.slice(index, index + 1),
                ...state.slice(index + 2)
            ]
        default:
            return state;
    }
}
