import React from "react";
import {connect, ConnectedProps, ReactReduxContext} from "react-redux";
import {RootState} from "./reducers";
import {Box, Button} from "@material-ui/core";
import SelektorGminy from "./SelektorGminy";
import SelektorWojewodztwa from "./SelektorWojewodztwa";
import { ReactInstance } from "react";
import { changeLogo } from "./konfiguracja/actions";
import {JSONCrush} from "jsoncrush";
import {FileCopyOutlined} from "@material-ui/icons";
// @ts-ignore
import shortHash from "short-hash";
import axios from "axios";
import { exportComponentAsPNG } from "react-component-export-image";

interface UdostepnijProps extends PropsFromRedux {
    componentRef: React.RefObject<ReactInstance>
}

class UdostepnijInternal extends React.Component<UdostepnijProps> {
  static HAPPI_API_KEY = "eaa67fSr0cY475zwvjScRDs3Sj9YvPz9LUfsBiVqloMFAL2AyqNpnCxu";

  // Wysanie zakodowanej treci posta do key-value storage aby zrobic krotki URL pod hashem
  postKey(hash: string, content: string) {
    const url = "https://api.happi.dev/v1/store/infografika.edu.pl/?apikey=" + UdostepnijInternal.HAPPI_API_KEY;
    const body = "space=infografika.edu.pl&key=" + hash + "&value=" + content;
    axios.post(url, body).then((response) => console.log(response));
  }

  render() {
      const {gmina} = this.props;
      const plikInfografiki = gmina + "-" + new Date().toLocaleDateString("pl-PL") + ".png";
      const hashEl = document.getElementById("hash");
      const shortUrlEl = document.getElementById("shortUrl")
      return <ReactReduxContext.Consumer>
        {({ store }) => {
          return <Box display="flex" flexDirection="column" className="accordionContent">
            <strong>Wybr Jednostki Samorzdu:</strong>
            <span className="hint">Wojewdztwo:</span>
            <SelektorWojewodztwa/>
            <span className="hint">Wywietl dane dla (Wojewdztwo / Powiat / Gmina):</span>
            <SelektorGminy porownawcze={false}/>

            <Button variant="contained" color="primary" style={{margin: "20px"}} onClick={() => {
              const {dane_gminy, lista_powiatow, dane_podregionu, porownawcze_dane_gminy, ...state} = store.getState();
              const content = JSONCrush(JSON.stringify(state));
              const hash = shortHash(window.location.href.split('?')[0] + '?s=' + content).slice(4);
              this.postKey(hash, content);
              // @ts-ignore
              document.getElementById("hash").innerText = "polska2050.edu.pl/" + hash;
              if (shortUrlEl) {
                shortUrlEl.hidden = false;
              }
              exportComponentAsPNG(
                this.props.componentRef, {fileName: plikInfografiki})
            }}>
              Pobierz plik i udostpnij na FB/IG
            </Button>
            <Box flexDirection="row" display="flex" justifyContent="center">
              <Box id="shortUrl" className="shortUrl" hidden={(hashEl !== undefined && hashEl !== null && hashEl.innerText.toString() === '')}><span id="hash"></span><Button onClick={() => {
                let str = document.getElementById("hash");
                if (str === null) {
                  return;
                }
                navigator.clipboard.writeText(str.innerText).then(function () {
                    console.log("Copied to clipboard");
                  }, function () {
                    console.log("Copy to clipboard failed!!");
                  }
                );
              }}><FileCopyOutlined/></Button>
              </Box>
            </Box>
          </Box>
        }
  }
      </ReactReduxContext.Consumer>;
  }
}

const mapStateToProps = (state: RootState) => {
    return {
        gmina: state.dane_gminy.gmina,
    };
};

const mapDispatchToProps = {
    changeLogo,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(UdostepnijInternal);